/* projects.css */
.projects-section {
  padding: 6rem 2rem;
  background: transparent;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.project-card {
  background: rgba(17, 34, 64, 0.5);
  border-radius: var(--border-radius-lg);
  padding: var(--spacing-md);
  transition: var(--transition);
  border: 1px solid rgba(100, 255, 218, 0.1);
}

.project-card:hover {
  transform: translateY(-5px);
}

.project-title {
  color: #e6f1ff;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.project-description {
  color: #8892b0;
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.project-tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
  margin-bottom: 1.5rem;
}

.tech-tag {
  background: rgba(35, 53, 84, 0.5);
  color: var(--secondary);
  padding: var(--spacing-xs) var(--spacing-sm);
  border-radius: var(--border-radius-sm);
  font-size: 0.9rem;
}

.project-links {
  display: flex;
  gap: 1rem;
}

.project-link {
  color: #64ffda;
  text-decoration: none;
  transition: color 0.3s ease;
}

.project-link:hover {
  color: #e6f1ff;
}

@media (max-width: 768px) {
  .projects-section {
    padding: 4rem 1rem;
  }
  
  .projects-grid {
    grid-template-columns: 1fr;
  }
}
