/* contact.css */
.contact-section {
  padding: 6rem 2rem;
  background: transparent;
  text-align: center;
}

.contact-section h2 {
  color: #e6f1ff;
}

form {
  max-width: 600px;
  margin: 0 auto;
  background: rgba(17, 34, 64, 0.5);
  padding: var(--spacing-md);
  border-radius: var(--border-radius-lg);
  border: 1px solid rgba(100, 255, 218, 0.1);
}

.form-group {
  margin-bottom: 2rem;
  text-align: left;
}

label {
  display: block;
  margin-bottom: 0.5rem;
  color: #64ffda;
  font-size: 1.1rem;
}

input,
textarea {
  width: 100%;
  padding: 0.8rem;
  border: 1px solid rgba(100, 255, 218, 0.1);
  border-radius: var(--border-radius-sm);
  font-family: inherit;
  font-size: 1rem;
  background: rgba(35, 53, 84, 0.5);
  color: #ccd6f6;
}

textarea {
  min-height: 150px;
  resize: vertical;
}

button {
  background: transparent;
  color: #64ffda;
  border: 1px solid #64ffda;
  padding: 1rem 2rem;
  font-family: inherit;
  font-size: 1rem;
  border-radius: var(--border-radius-sm);
  cursor: pointer;
  transition: all 0.3s ease;
}

button:hover {
  background: rgba(100, 255, 218, 0.1);
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .contact-section {
    padding: 4rem 1rem;
  }
  
  input,
  textarea {
    font-size: 16px; /* Prevents zoom on mobile */
  }
}

input:focus,
textarea:focus {
  background: rgba(35, 53, 84, 0.7);
  border-color: #64ffda;
  outline: none;
}
