/* skills.css */
.skills {
  padding: 50px 0;
  background-color: #f8f8f8;
}

.skills h2 {
  font-size: 1.75rem;
  margin-bottom: 30px;
  color: #333;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
}

.skills-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.skills-list li {
  background: #fff;
  margin: 10px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  flex: 0 1 calc(33% - 40px);
  text-align: center;
  transition: transform 0.3s, box-shadow 0.3s, background-color 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skills-list li img {
  width: 60px;
  height: 60px;
  object-fit: contain;
  margin-bottom: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.skills-list li span {
  font-size: 1rem;
  color: #333;
}

.skills-list li:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  background-color: #f0f8ff;
}

.skills-section {
  padding: 6rem 2rem;
  background: transparent;
}

.skills-section h2 {
  text-align: center;
  margin-bottom: 4rem;
  font-size: 2.5rem;
  color: #2d3436;
}

.skills-container {
  max-width: 1000px;
  margin: 0 auto;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.skill-category {
  background: rgba(17, 34, 64, 0.5);
  border-radius: var(--border-radius-lg);
  padding: var(--spacing-md);
  border: 1px solid rgba(100, 255, 218, 0.1);
}

.skill-category h3 {
  color: #64ffda;
  margin-bottom: 1.5rem;
  font-size: 1.3rem;
}

.skill-item {
  color: #8892b0;
  padding: 0.5rem 0;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.skill-item::before {
  content: "â–¹";
  color: #64ffda;
}

.skill-level {
  margin-top: 0.5rem;
  height: 4px;
  background: rgba(35, 53, 84, 0.5);
  border-radius: var(--border-radius-sm);
  overflow: hidden;
}

.skill-progress {
  height: 100%;
  background: #64ffda;
  border-radius: 2px;
  transition: width 0.3s ease;
}

@media (max-width: 768px) {
  .skills-section {
    padding: 4rem 1rem;
  }
  
  .skills-grid {
    grid-template-columns: 1fr;
  }
}
